@font-face {
  font-family: "Human";
  src: url("C:/Users/dozan/Documents/react/failing-forward-website/src/fonts/humanist521bt_roman.ttf") format("truetype");
}
* {
  margin: 0;
  padding: 0;
  font-family: "Human", "Roboto", "san Francisco";
  color: #323E67;
  cursor: default;
  text-align: center;
}

@media (max-width: 800px) {
  h1 {
    font-size: calc((1.375rem + 1.5vw) / 1.3);
  }
  span {
    font-size: calc((1.375rem + 1.5vw) / 1.3);
  }
  h2 {
    font-size: calc((1.325rem + 0.9vw) / 1.4);
  }
}
.App {
  overflow: hidden;
  background-color: #323E67;
  background-image: url("C:/Users/dozan/Documents/react/failing-forward-website/src/images/bg.svg");
  background-size: cover;
}

.main {
  width: 1140px;
  max-width: 100%;
  height: 100%;
  margin: auto;
  background-color: white;
}

.feature, .page-part {
  width: 100%;
  margin: 100px auto;
}
.feature .featureTitle, .page-part .featureTitle {
  text-align: center;
}
.feature .featureIcon, .page-part .featureIcon {
  height: 200px;
  width: 100%;
  margin: 20px auto 0 auto;
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
@media (max-width: 800px) {
  .feature, .page-part {
    margin: 60px auto;
  }
  .feature .featureIcon, .page-part .featureIcon {
    width: 80%;
    height: auto;
    display: block;
    margin: 5px auto 0 auto;
  }
  .feature .featureTitle, .page-part .featureTitle {
    width: 80%;
    height: auto;
    display: block;
    margin: 5px auto 0 auto;
  }
}

.page-part {
  margin-top: 0;
  position: relative;
  top: 0;
  left: 0;
}
.page-part #circles {
  margin-top: 10px;
  left: 48%;
  transform: translateX(-50%);
  position: relative;
  width: 1500px;
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.page-part #logo {
  margin-top: 10px;
  position: absolute;
  left: 50%;
  top: 30%;
  transform: translate(-50%, -50%);
  width: 600px;
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.page-part h1 {
  text-align: center;
  position: absolute;
  left: 50%;
  top: 70%;
  transform: translate(-50%, -50%);
  width: 600px;
}
.page-part h1 span {
  background: linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
@media (max-width: 800px) {
  .page-part #circles {
    opacity: 0;
    width: 0;
    height: 0;
  }
  .page-part h1 {
    text-align: center;
    position: static;
    display: block;
    transform: none;
    width: 100%;
  }
  .page-part #logo {
    width: 90%;
    position: static;
    transform: none;
    margin: 0 auto;
    display: block;
  }
}

.game-section {
  margin: 100px auto;
}
.game-section h1 {
  margin: 10px 0 20px 0;
}
.game-section h2 {
  margin: 20px 0 10px 0;
}

.thankyou_message {
  display: none;
  margin: auto auto auto auto;
  height: 200px;
}

.form-elements {
  text-align: center;
}
.form-elements h1 {
  margin: 0 0 20px 0;
}
.form-elements h2 {
  margin: 10px 0 20px 0;
  color: #3795F2;
}
.form-elements div {
  margin: auto auto auto auto;
  text-align: start;
  width: 390px;
}
.form-elements label {
  font-weight: bold;
  font-size: 22px;
  display: block;
  text-align: start;
  margin: 20px 10px 13px 0;
  width: 360px;
}
.form-elements input, .form-elements textarea {
  border: 1px solid rgb(211, 222, 255);
  border-radius: 10px;
  width: 360px;
  height: 35px;
  text-align: start;
  padding: 5px 15px;
  font-size: 22px;
  box-shadow: inset 0 0 3px #aaaaaa;
}
.form-elements textarea {
  padding: 10px 15px;
  height: 300px;
  resize: none;
}
.form-elements button {
  margin: 25px 0 150px 0;
  border: none;
  background-color: #3795F2;
  width: 350px;
  height: 50px;
  border-radius: 10px;
  font-size: 28px;
  color: white;
  box-shadow: 0 5px 20px #a7a7a7;
  transition: all 200ms ease;
}
.form-elements button:hover {
  box-shadow: 0 0px 8px #a7a7a7;
  transform: scale(0.985);
  background-color: #323E67;
  cursor: pointer;
}
.form-elements input:focus, .form-elements textarea:focus {
  border: 1px solid rgb(211, 222, 255);
  outline: none;
}
@media (max-width: 800px) {
  .form-elements button {
    width: 90%;
  }
  .form-elements input, .form-elements textarea {
    width: 92%;
  }
  .form-elements div {
    width: 90%;
  }
  .form-elements label, .form-elements input, .form-elements textarea {
    font-size: calc((1.325rem + 0.9vw) / 1.4);
  }
}

.basic-grid, .basic-grid-paid-for {
  display: grid;
  gap: 1rem;
  width: 80%;
  margin: auto auto;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}
.basic-grid *, .basic-grid-paid-for * {
  color: white;
}

@media (max-width: 800px) {
  .basic-grid, .basic-grid-paid-for {
    width: 90%;
    margin: auto auto;
    grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  }
  .basic-grid *, .basic-grid-paid-for * {
    color: white;
  }
  .basic-grid-paid-for {
    grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  }
}
.card-tall {
  grid-row: span 2/auto;
}

.card-wide {
  grid-column: span 2/auto;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #e7e7e7;
  color: #fff;
  box-shadow: rgba(3, 8, 20, 0.1) 0px 0.15rem 0.5rem, rgba(2, 8, 20, 0.1) 0px 0.075rem 0.175rem;
  height: 100%;
  width: 100%;
  border-radius: 4px;
  transition: all 500ms;
  overflow: hidden;
}

.card:hover {
  box-shadow: rgba(2, 8, 20, 0.1) 0px 0.35em 1.175em, rgba(2, 8, 20, 0.08) 0px 0.175em 0.5em;
  transform: translateY(-3px) scale(1.05);
}

.card img {
  transition: all 500ms;
  position: center;
  -o-object-fit: cover;
     object-fit: cover;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
}

.card:hover img {
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
}

.card p {
  visibility: hidden;
  opacity: 0;
  transition: all 500ms;
  width: 200px;
  height: 50px;
  vertical-align: middle;
  text-align: center;
  text-shadow: 1px 1px 3px #353535;
  position: absolute;
  top: 50;
  left: 50;
}

@media (max-width: 800px) {
  .card p {
    font-size: calc((1.325rem + 0.9vw) / 2);
    width: 150px;
  }
}
.card:hover p {
  visibility: visible;
  opacity: 1;
}

.dropdown {
  margin: 50px auto 0 auto;
  padding: 20px;
  max-width: 300px;
  font-size: 2.5rem;
  text-align: center;
  color: white;
}

@media (max-width: 800px) {
  .dropdown {
    margin: 1vh auto 0 auto;
    width: 25vw;
    font-size: calc((1.375rem + 1.5vw) / 1.4);
  }
}
.Dropdown-root {
  position: relative;
  transition: all 0.2s ease-in-out;
  -webkit-animation-name: dropdown-button-anim;
          animation-name: dropdown-button-anim;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.Dropdown-root:hover {
  transform: scale(1.1);
  -webkit-animation: none;
          animation: none;
  border-radius: 30px;
}

@-webkit-keyframes dropdown-button-anim {
  from {
    transform: scale(1);
  }
  25% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1);
  }
  to {
    transform: scale(1);
  }
}

@keyframes dropdown-button-anim {
  from {
    transform: scale(1);
  }
  25% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1);
  }
  to {
    transform: scale(1);
  }
}
.Dropdown-control {
  background: linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) padding-box, linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) border-box;
  position: relative;
  overflow: hidden;
  background-color: white;
  border: 2px solid transparent;
  border-radius: 40px;
  box-sizing: border-box;
  color: WHITE;
  cursor: pointer;
  outline: none;
  transition: all 200ms ease;
}

.Dropdown-placeholder {
  color: white;
  cursor: pointer;
}

.is-open .Dropdown-arrow {
  border-color: transparent transparent #999;
  border-width: 0 5px 5px;
}

.Dropdown-menu {
  background: linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) padding-box, linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) border-box;
  background-color: white;
  border: 0px solid transparent;
  border-radius: 15px;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  margin-top: -1px;
  max-height: auto;
  overflow-y: auto;
  position: absolute;
  top: 200%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  z-index: 1000;
  -webkit-overflow-scrolling: touch;
  transition: all 200ms ease;
}

@media (max-width: 800px) {
  .Dropdown-menu {
    width: 25vw;
    top: 165%;
  }
}
.Dropdown-menu .Dropdown-group > .Dropdown-title {
  padding: 8px 10px;
  color: white;
  font-weight: bold;
  text-transform: capitalize;
}

.Dropdown-option {
  box-sizing: border-box;
  color: rgba(51, 51, 51, 0.5);
  cursor: pointer;
  display: block;
  padding: 8px 10px;
  transition: all 200ms ease;
}

.Dropdown-option:last-child {
  border-bottom-right-radius: 2px;
  border-bottom-left-radius: 2px;
}

.Dropdown-option:hover {
  background-color: #313ea5;
  color: white;
}

.Dropdown-option.is-selected {
  background-color: #313ea5;
  color: white;
}

.Dropdown-noresults {
  box-sizing: border-box;
  color: #ccc;
  cursor: default;
  display: block;
  padding: 8px 10px;
}/*# sourceMappingURL=style.css.map */