$mobileWidth: 800px;
$primary-color: #323E67;
$secondary-color: #3795F2;

@mixin mobile {
    @media (max-width: $mobileWidth) {
        @content;
    }
}
@mixin nondraggable {
    user-drag: none;
    -webkit-user-drag: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}

@font-face {
    font-family: 'Human';
    src: url('C:/Users/dozan/Documents/react/failing-forward-website/src/fonts/humanist521bt_roman.ttf')  format('truetype')
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Human', 'Roboto', 'san Francisco';
    color: $primary-color;
    cursor: default;
    text-align: center;
}
@include mobile {
    h1 {
        font-size: calc((1.375rem + 1.5vw)/1.3);
    }
    span{
        font-size: calc((1.375rem + 1.5vw)/1.3);
    }
    h2 {
        font-size: calc((1.325rem + 0.9vw)/1.4);
    }
}
.App {
    overflow: hidden;
    background-color: #323E67;
    background-image: url( 'C:/Users/dozan/Documents/react/failing-forward-website/src/images/bg.svg');
    background-size: cover;
}
.main {
    width: 1140px;
    max-width: 100%;
    height: 100%;
    margin: auto;
    background-color: white;
}
.feature {
    width: 100%;
    margin: 100px auto;
    .featureTitle {
        text-align: center;
    }
    .featureIcon {
        height: 200px;
        width: 100%;
        margin: 20px auto 0 auto;
        @include nondraggable;
    }
    @include mobile {
        margin: 60px auto;
        .featureIcon {
            width: 80%;
            height: auto;
            display: block;
            margin: 5px auto 0 auto;
        }
        .featureTitle {
            width: 80%;
            height: auto;
            display: block;
            margin: 5px auto 0 auto;
        }
    }
}
.page-part {
    @extend .feature;
    margin-top: 0;
    position: relative;
    top: 0;
    left: 0;

    #circles {
        margin-top: 10px;
        left: 48%;
        transform: translateX(-50%);
        position: relative;
        width: 1500px;
        @include nondraggable;
    }
    #logo {
        margin-top: 10px;
        position: absolute;
        left: 50%;
        top: 30%;
        transform: translate(-50%,-50%);
        width: 600px;
        @include nondraggable;
    }
    h1 {
        text-align: center;
        position: absolute;
        left: 50%;
        top: 70%;
        transform: translate(-50%,-50%);
        width: 600px;
        span {
            background: linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
        }
    }
    @include mobile {
        #circles {
            opacity: 0;
            width: 0;
            height: 0;
        }
        h1 {
            text-align: center;
            position: static;
            display: block;
            transform: none;
            width: 100%;
        }
        #logo {
            width: 90%;
            position: static;
            transform: none;
            margin: 0 auto;
            display: block;
        }
    }
}
.game-section {
    h1 {
        margin: 10px 0 20px 0;
    }
    h2 {
        margin: 20px 0 10px 0;
    }
    margin: 100px auto;
}
.thankyou_message {
    display: none;
    margin: auto auto auto auto;
    height: 200px;
}
.form-elements {
    text-align: center;
    h1 {
        margin: 0 0 20px 0;
    }
    h2 {
        margin: 10px 0 20px 0;
        color: $secondary-color;
    }
    div {
        margin: auto auto auto auto;
        text-align: start;
        width: 390px;
    }
    label {
        font-weight: bold;
        font-size: 22px;
        display: block;
        text-align: start;
        margin: 20px 10px 13px 0;
        width: 360px;
    }
    input {
        border: 1px solid rgb(211, 222, 255);
        border-radius: 10px;
        width: 360px;
        height: 35px;
        text-align: start;
        padding: 5px 15px;
        font-size: 22px;
        box-shadow: inset 0 0 3px #aaaaaa;
    }
    textarea {
        @extend input;
        padding: 10px 15px;
        height: 300px;
        resize: none;
    }
    button {
        margin: 25px 0 150px 0;
        border: none;
        background-color: $secondary-color;
        width: 350px;
        height: 50px;
        border-radius: 10px;
        font-size: 28px;
        color: white;
        box-shadow: 0 5px 20px #a7a7a7;
        transition: all 200ms ease;
    }
    button:hover {
        box-shadow: 0 0px 8px #a7a7a7;
        transform: scale(0.985);
        background-color: $primary-color;
        cursor: pointer;
    }
    input:focus, textarea:focus {
        border: 1px solid rgb(211, 222, 255);
        outline: none;
    }
    @include mobile {
        button {
            width: 90%;
        }
        input {
            width: 92%;
        }
        div {
            width: 90%;
        }
        label, input {
            font-size: calc((1.325rem + 0.9vw)/1.4);
        }
    }
}

//------------------------СТАРЫЕ СТИЛИ--------------------------------------------
.basic-grid {
    display: grid;
    gap: 1rem;
    width: 80%;
    margin: auto auto;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    * {
        color: white;
    }
  }
.basic-grid-paid-for {
    @extend .basic-grid;
}
@include mobile {
    .basic-grid {
        width: 90%;
        margin: auto auto;
        grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
        * {
            color: white;
        }
      }
    .basic-grid-paid-for {
        grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
    }
}
  .card-tall {
    grid-row: span 2 / auto;
  }
  .card-wide {
    grid-column: span 2 / auto;
  }
  .card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #e7e7e7;
    color: #fff;
    box-shadow: rgba(3, 8, 20, 0.1) 0px 0.15rem 0.5rem, rgba(2, 8, 20, 0.1) 0px 0.075rem 0.175rem;
    height: 100%;
    width: 100%;
    border-radius: 4px;
    transition: all 500ms;
    overflow: hidden;
 }
.card:hover {
    box-shadow: rgba(2, 8, 20, 0.1) 0px 0.35em 1.175em, rgba(2, 8, 20, 0.08) 0px 0.175em 0.5em;
    transform: translateY(-3px) scale(1.05);
}
.card img {
  transition: all 500ms;
  position: center;
  object-fit: cover;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
}
.card:hover img {
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    filter: blur(5px);
 }
 .card p {
    visibility: hidden;
    opacity: 0;
    transition: all 500ms;
    width: 200px;
    height: 50px;
    vertical-align: middle;
    text-align: center;
    text-shadow: 1px 1px 3px #353535;

    position: absolute;
    top: 50;
    left: 50;
 }
 @include mobile {
    .card p {
        font-size: calc((1.325rem + 0.9vw)/2);
        width: 150px;
    }
 }
 .card:hover p {
    visibility: visible;
    opacity: 1;
  }
.dropdown {
    margin: 50px auto 0 auto;
    padding: 20px;
    max-width: 300px;
    font-size: 2.5rem;
    text-align: center;
    color: white;
  }
@include mobile {
    .dropdown {
        margin: 1vh auto 0 auto;
        width: 25vw;
        font-size: calc((1.375rem + 1.5vw)/1.4);
    }
}
  .Dropdown-root {
    position: relative;
    transition: all .2s ease-in-out;
    animation-name: dropdown-button-anim;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
  }
  .Dropdown-root:hover {
    transform: scale(1.1);
    animation: none;
    border-radius: 30px;
}
    @keyframes dropdown-button-anim {
        from {
            transform: scale(1);
        }
        25% {
            transform: scale(1.1);
        }
        50% {
            transform: scale(1);
        }
        to {
            transform: scale(1);
        }
    }
  
  .Dropdown-control {
    background: linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) padding-box,
                linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) border-box;
  
    position: relative;
    overflow: hidden;
    background-color: white;
    border: 2px solid transparent;
    border-radius: 40px;
    box-sizing: border-box;
    color: WHITE;
    cursor: pointer;
    outline: none;
    transition: all 200ms ease;
  }
  .Dropdown-placeholder{
    color: white;
    cursor: pointer;
  }
  .is-open .Dropdown-arrow {
    border-color: transparent transparent #999;
    border-width: 0 5px 5px;
  }
  
  .Dropdown-menu {
    background: linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) padding-box,
                linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%) border-box;
    background-color: white;
    border: 0px solid transparent;
    border-radius: 15px;
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
    box-sizing: border-box;
    margin-top: -1px;
    max-height: auto;
    overflow-y: auto;
    position: absolute;
    top: 200%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 80%;
    z-index: 1000;
    -webkit-overflow-scrolling: touch;
    transition: all 200ms ease;
  }
  @include mobile {
    .Dropdown-menu {
        width: 25vw;
        top: 165%;
    }
  }
  
  .Dropdown-menu .Dropdown-group > .Dropdown-title{
    padding: 8px 10px;
    color: white;
    font-weight: bold;
    text-transform: capitalize;
  }
  
  .Dropdown-option {
    box-sizing: border-box;
    color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    display: block;
    padding: 8px 10px;
    transition: all 200ms ease;
  }
  
  .Dropdown-option:last-child {
    border-bottom-right-radius: 2px;
     border-bottom-left-radius: 2px;
  }
  
  .Dropdown-option:hover {
    background-color: #313ea5;
    color: white;
  }
  
  .Dropdown-option.is-selected {
    background-color: #313ea5;
    color: white;
  }
  
  .Dropdown-noresults {
    box-sizing: border-box;
    color: #ccc;
    cursor: default;
    display: block;
    padding: 8px 10px;
  }